<template>
    <div id="app">
        <div class="sign-bg">
			<div class="signup-tabs">
				<div class="signup-container">
					<div class="signup-tab">
						<span>补充注册信息</span>
						<i class="icon-tri"></i>
					</div>
				</div>
			</div>
		</div>
		<div class="signup-form">
			<input type="" name="" v-model="nickname" placeholder="请输入昵称" class="input-sign">
			<input type="password" name="" v-model="password" placeholder="请输入密码" class="input-sign">
			<button class="button-default button-signup" v-on:click="nextHandler">下一步</button>
		</div>
		<transition name="fade">
			<Toast v-bind:msg="toastText" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>

<script>
import Hello from '../../components/Hello'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toastText: '',
    		nickname: '',
	    	password: ''
    	}
    },
    methods:{
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 3000);
    	},
    	nextHandler: function(){
    		var that = this;
    		var nickname = utils.trim(that.nickname);
            var password = utils.trim(that.password);
            if(nickname == '' || nickname == undefined){
                that.toastText = '昵称不能为空！';
                that.showToast();
                return;
            }
            if(password == '' || password == undefined){
                that.toastText = '密码不能为空！';
                that.showToast();
                return;
            }
            store.supplementary_info(nickname, password).then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    location.href = store.site_links.index;
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
    	}
    },
    components: {
    	Hello,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/sign.css';
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
</style>



